import React, { Component } from "react";
import { Layout, Menu, Icon, Avatar, Button, Affix } from "antd";
import { withRouter } from "react-router-dom";
import "../styles/style.scss";
import logo from "../asset/photo.jpeg";
import { observer, inject } from "mobx-react";

const { Header, Sider, Content, Footer } = Layout;
const { SubMenu } = Menu;

@inject("menuStore")
@observer
class MainLayout extends Component {
  state = {
    collapsed: false
  };

  onCollapse = () => {
    this.setState({
      collapsed: !this.state.collapsed
    });
  };

  goto = e => {
    console.log(e);
    this.props.history.push(e.key);
  };

  switchMenuStyle = () => {
    this.props.menuStore.toogleMenuStyle();
  };

  render() {
    const { isLeftMenu } = this.props.menuStore;
    return (
      <Layout>
        <Header className="layout-header">
          <div className={!isLeftMenu ? "top-menu-logo" : "left-menu-logo"}>
            <Avatar src={logo} />
            <span
              style={{
                color: "white",
                fontWeight: "bold",
                padding: "10px 0 0 15px"
              }}
            >
              古时的风筝
            </span>
            {isLeftMenu && (
              <Button
                type="default"
                onClick={this.onCollapse}
                style={{ marginLeft: "30px", marginBottom: 16 }}
              >
                <Icon
                  type={this.state.collapsed ? "menu-unfold" : "menu-fold"}
                />
              </Button>
            )}
          </div>
          {!isLeftMenu && (
            <div style={{ float: "left" }}>
              <Menu
                theme="dark"
                mode="horizontal"
                onClick={this.goto}
                defaultSelectedKeys={["/"]}
                style={{ lineHeight: "64px", width: "500px" }}
              >
                <Menu.Item key="/">
                  <Icon type="pie-chart" />
                  <span>仪表盘</span>
                </Menu.Item>
                <Menu.Item key="domain">
                  <Icon type="apartment" />
                  <span>MBeans</span>
                </Menu.Item>
              </Menu>
            </div>
          )}
          <div style={{ float: "right" }}>
            <Button
              onClick={this.switchMenuStyle}
              className="switch-menu-btn"
              ghost
            >
              切换导航风格
            </Button>
          </div>
        </Header>
        <Layout>
          {isLeftMenu && (
            <Sider
              width={200}
              style={{ background: "#fff" }}
              collapsed={this.state.collapsed}
            >
              <Menu
                mode="inline"
                theme="dark"
                onClick={this.goto}
                defaultSelectedKeys={["/"]}
                style={{ height: "100%", borderRight: 0 }}
              >
                <Menu.Item key="/">
                  <Icon type="pie-chart" />
                  <span>仪表盘</span>
                </Menu.Item>
                <Menu.Item key="domain">
                  <Icon type="apartment" />
                  <span>MBeans</span>
                </Menu.Item>
              </Menu>
            </Sider>
          )}
          <Layout>
            <Content style={{ margin: "10px 16px" }}>
              <Affix style={{ position: "absolute", top: 70, right: 30 }}>
                <div >
                  <Icon
                    type="api"
                    theme="twoTone"
                    style={{padding:"10px",borderRadius:"50%",border:"1px solid #108ee9",fontSize: "48px"}}
                  />
                </div>
              </Affix>
              <div style={{ padding: 24, background: "#fff", minHeight: 360 }}>
                {this.props.children}
              </div>
            </Content>
            <Footer style={{ textAlign: "center" }}>古时的风筝</Footer>
          </Layout>
        </Layout>
      </Layout>
    );
  }
}

export default withRouter(MainLayout);
